<template>
  <div>
    <div class="wrapper">
    <v-col> 
        <v-card class="mx-auto" max-width="400"> 
         <div align="center">
             <v-img 
              width="200px"
              src="https://i.ibb.co/fHjjz2X/logo.png"
            >
            </v-img>
          </div>
          <v-card-text>
          <v-text-field 
              :append-icon="'mail'"
              color = "blue"
              v-model="user"
              outlined
              clear-icon="mdi-close-circle"
              clearable
              label="Correo electrónico"
              type="text"
              >
            </v-text-field>
            <v-text-field 
              color="blue"
              v-model="password"
              outlined
              clear-icon="mdi-close-circle"
              clearable
              label="Contraseña"
              :type="show1 ? 'text' : 'password'"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="show1 = !show1"
              >
              
            </v-text-field>
            <v-card-actions class="justify-center">
              <v-btn style="width:370px" color="blue" class="white--text" :disabled="!password" @click="conCuenta()" >Iniciar sesión</v-btn>
            </v-card-actions>
           
         </v-card-text>
        
        <v-card-text align="center">
            <a @click="contrasenia()"  v-bind:value="false" class="font-weight-bold" style="text-decoration:none">¿Has olvidado la contraseña?</a>
         
         </v-card-text>
          
        </v-card>
        </v-col>
        <v-col>
        <v-card class="mx-auto" max-width="400">
         <v-card-text align="center"> 
           ¿No tienes una cuenta?  
           <div>
           <a @click="registro()"  v-bind:value="false">Regístrate</a>
          </div >
         </v-card-text>
        </v-card>
        <br><br>
        </v-col>
    </div>
    <v-footer
                dark
                padless
                class="footer"
                >
                <v-card
                flat
                tile
                class="blue darken-1 white--text text-center flex">
                    <v-card-text>
                        2020 Aparcar Inc
                    </v-card-text>
                    <v-card-text>
                        Contacta con nosotros: <v-icon  size="24px">  mdi-instagram</v-icon> <v-icon  size="24px">mdi-facebook</v-icon><v-icon size="24px">mdi-twitter</v-icon>
                    </v-card-text>
                </v-card>
                </v-footer>   

  </div>
</template>

<script>
  export default {
    name: 'Inicio',

    data: () => ({

      tab: null,
      model: 1,
      password: '',
      user: '',
      show1:false,
      vue: {
        exp:'false'
      }
     
    }),
    methods:{
      contrasenia(){
        this.$router.push('contrasenia');
      },
      conCuenta(){
        this.$store.commit('updateSession', true);
        this.$router.push('cuenta');

      },
      registro(){
        this.$router.push('registro');
      }
    },

  }
</script> 

<style scoped lang="scss">
    .wrapper {
      min-height: calc(100vh - 175px);
    }
    .footer {
      position: relative;
      width: 100%;
      bottom: 0;    
    }
    
</style>
