<template>
  <div>
  <div class="wrapper">
    <v-card  class="mx-auto" max-width="400" >
          <v-content>
              <v-layout align-center justify-center> 
                <v-flex>
                  <v-card-text>
                    <div align="center">
                      <v-img 
                        width="200px"
                        src="https://i.ibb.co/fHjjz2X/logo.png"
                      >
                      </v-img>

                      <p  class ="grey--text" align="center" justify-center style = "font-family: arial; width:300px"> <b>Reservar la plaza</b></p>
                    </div>
                      <br>
                      <v-text-field color = "blue" v-model="name"  clear-icon="mdi-close-circle" clearable label = "Nombre y apellidos" type="text"  outlined ></v-text-field>

                      <v-text-field color = "blue" v-model="email"  outlined clear-icon="mdi-close-circle" clearable label = "Correo electrónico" type="text" ></v-text-field>
                      <v-text-field color = "blue" v-model="dni" clear-icon="mdi-close-circle" clearable label ="DNI" type="text" outlined ></v-text-field>
                      <v-text-field color = "blue" v-model="matricula"  clear-icon="mdi-close-circle" clearable  label = "Matricula" type="text" outlined ></v-text-field>
                      <v-select color = "blue" v-model="tipoV" clear-icon="mdi-close-circle" clearable :items="tiposVehiculos" label="Tipo de vehículo"></v-select>
                      <v-text-field color = "blue" v-model="inicio"  clear-icon="mdi-close-circle" clearable  label = "Inicio de reserva" type="time" outlined ></v-text-field>
                      <v-text-field color = "blue" v-model="fin"  clear-icon="mdi-close-circle" clearable  label = "Fin de reserva" type="time" outlined ></v-text-field>
                        <v-radio-group v-model="formaPago">
                        <v-radio
                            v-for="pago in pagos"
                            :key="pago"
                            :label="pago"
                            :value="pago"
                        ></v-radio>
                        </v-radio-group>
                      <v-text-field color = "blue" v-model="tarjeta"  clear-icon="mdi-close-circle" clearable  label = "Numero de tarjeta" type="time" outlined ></v-text-field>

                    

                     <v-card-actions class="justify-center">
                        <v-btn color="blue" class="white--text"  @click="dialog=true">Reservar</v-btn> <br>
                        <v-spacer></v-spacer>
                        <v-btn color="blue" class="white--text" @click="cancelar()">Cancelar</v-btn>
                     </v-card-actions>
                    <br>
                  </v-card-text>
                </v-flex>
              </v-layout>
          </v-content>
  
      
    </v-card>
    <br><br>
  </div>
    <v-footer
                dark
                padless
                class="footer"
                >
                <v-card
                flat
                tile
                class="blue darken-1 white--text text-center flex">
                    <v-card-text>
                        2020 Aparcar Inc
                    </v-card-text>
                    <v-card-text>
                        Contacta con nosotros: <v-icon  size="24px">  mdi-instagram</v-icon> <v-icon  size="24px">mdi-facebook</v-icon><v-icon size="24px">mdi-twitter</v-icon>
                    </v-card-text>
                </v-card>
                </v-footer>  
    <v-dialog v-model="dialog">
      <v-card>
				<v-card-title class="headline">
				¡Reserva correcta! 
				</v-card-title>
				<v-card-text>Su reserva se ha realizado correcatmente</v-card-text> 
				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn
						color="blue darken-1"
						text
						@click="dialog=false"
					>Cerrar
					</v-btn>
				</v-card-actions>
			</v-card>
    </v-dialog>
    
  </div>
</template>

<script>
  

export default {


  data: () => ({
    name:'',
    email:'',
    matricula:'',
    dni:'',
    tipoV:'',
    inicio:'',
    fin:'',
    formaPago:'',
    tarjeta:'',
    tiposVehiculos:['Coche','Moto'],
    pagos:['Visa','MasterCard'],
    n:'',
    p:'',
    dialog:false,
    
  }),
  
  methods:{
    cancelar(){
      
      var id = this.$route.params.parking;
      this.$router.push({
        name:"plazas",
        params:{
					id: id,
				},
      });
    }
  },
  
}
</script>
<style scoped>
#o hr{ display:inline-block; }
 .wrapper {
      min-height: calc(100vh - 10px);
    }
    .footer {
      position: relative;
      width: 100%;
      bottom: 0;    
    }
    

</style>