. Al igual que muchos otros servicios, usamos los píxeles para averiguar si has interactuado con determinado contenido web o contenido de correo electrónico. Esto nos ayuda a medir y mejorar nuestros servicios y a personalizar tu experiencia. El almacenamiento local es una tecnología estándar de la industria que permite que un sitio web o una aplicación almacene información de forma local en tu computadora o dispositivo móvil. Usamos el almacenamiento local para personalizar lo que te mostramos, en función de tus interacciones anteriores con nuestros servicios. ")]),_c('h3',{staticStyle:{"margin":"30px"}},[_c('v-icon',{staticClass:"blue--text",attrs:{"color":"black"}},[_vm._v(" mdi-check-circle")]),_vm._v(" ¿Por qué nuestros servicios usan estas tecnologías? ")],1),_c('p',{staticStyle:{"margin":"50px","text-align":"justify"}},[_vm._v(" Nuestros servicios usan estas tecnologías para distribuir, medir y mejorar nuestros servicios de diversas maneras. Generalmente, estos usos corresponden a una de las siguientes categorías: ")]),_c('h4',{staticStyle:{"margin":"50px"}},[_c('v-icon',{staticClass:"blue--text",attrs:{"color":"black"}},[_vm._v(" mdi-check")]),_vm._v("Autenticación y seguridad: ")],1),_c('p',{staticStyle:{"margin":"70px","text-align":"justify"}},[_vm._v("Para iniciar tu sesión de Aparcar y Periscope. Para proteger tu seguridad. Para que puedas ver contenido de distribución limitada. Para ayudarnos a detectar y combatir el spam, el abuso y otras actividades que incumplen las Reglas de Aparcar y las Reglas de la comunidad de Periscope. Por ejemplo, estas tecnologías ayudan a autentificar tu acceso a Aparcar y a Periscope, y a evitar que otras personas no autorizadas puedan acceder a tu cuenta. También nos permiten mostrarte contenido apropiado a través de nuestros servicios.")]),_c('h4',{staticStyle:{"margin":"50px"}},[_c('v-icon',{staticClass:"blue--text",attrs:{"color":"black"}},[_vm._v(" mdi-check")]),_vm._v("Preferencias:")],1),_c('p',{staticStyle:{"margin":"70px","text-align":"justify"}},[_vm._v(" Para recordar información sobre tu navegador y tus preferencias. Por ejemplo, las cookies nos ayudan a recordar el idioma que prefieres o el país en el que te encuentras. De este modo, podemos proporcionarte contenido de Aparcar y de Periscope en tu idioma preferido sin tener que preguntarte cada vez que visites Aparcar o Periscope. En Aparcar, también podemos personalizar el contenido según tu país. Podemos mostrarte los temas que son tendencia cerca de tu ubicación o retener determinado contenido en función de las leyes locales aplicables. Obtén más información sobre las tendencias y el contenido retenido por el país.")]),_c('h4',{staticStyle:{"margin":"50px"}},[_c('v-icon',{staticClass:"blue--text",attrs:{"color":"black"}},[_vm._v(" mdi-check")]),_vm._v(" Estadísticas e investigación:")],1),_c('p',{staticStyle:{"margin":"70px","text-align":"justify"}},[_vm._v("Para ayudarnos a mejorar y comprender cómo la gente usa nuestros servicios, incluidos los botones y las herramientas de Aparcar, así como Aparcar Ads. Por ejemplo, las cookies nos ayudan a probar diferentes versiones de nuestros servicios para ver qué características o contenidos específicos prefieren los usuarios. También podemos optimizar y mejorar tu experiencia en Aparcar y Periscope mediante el uso de cookies para saber de qué forma interactúas con nuestros servicios, como analizar cuándo y con qué frecuencia los utilizas y en qué enlaces haces clic. Es posible que utilicemos Google Analytics para facilitar este proceso. Obtén más información sobre las cookies que puedes encontrar en relación con el uso de Google Analytics. También es posible que usemos cookies para contabilizar el número de usuarios que vieron determinadas cronologías o Tweets insertados. Obtén más información sobre las cookies de estadísticas utilizadas por las herramientas de Aparcar para sitios web.")]),_c('h4',{staticStyle:{"margin":"50px"}},[_c('v-icon',{staticClass:"blue--text",attrs:{