 dispositivos:")],1),_c('p',{staticStyle:{"margin":"70px","text-align":"justify"}},[_vm._v("Si comprendemos mejor cómo se relacionan los dispositivos, podemos usar la información de un dispositivo para personalizar la experiencia en Aparcar en otro dispositivo. Cuando inicias sesión en Aparcar desde un dispositivo, asociamos ese dispositivo a tu cuenta de Aparcar. Independientemente de si accediste o no a tu cuenta de Aparcar, es posible que también recibamos información sobre tus dispositivos en casos como los siguientes: cuando un socio comparte dicha información o cuando visitas Aparcar.com, sitios web de terceros que integran contenido de Aparcar, o el sitio web o la aplicación móvil de un anunciante de Aparcar. Podemos usar esta información (generalmente, las direcciones IP y la hora a la que se recibió la información) para inferir que determinados dispositivos están asociados entre sí, incluidos los dispositivos en los que inicias sesión en Aparcar. Para obtener más información acerca de los dispositivos asociados a tu cuenta, consulta Tus datos de Aparcar habiendo iniciado sesión en tu cuenta. Para obtener más información sobre los demás dispositivos asociados al dispositivo o al navegador que utilizas actualmente, visita Tus datos de Aparcar sin haber iniciado sesión en tu cuenta. ")]),_c('h3',{staticStyle:{"margin":"30px"}},[_c('v-icon',{staticClass:"blue--text",attrs:{"color":"black"}},[_vm._v(" mdi-check-circle")]),_vm._v("¿Cuáles son mis opciones de privacidad?")],1),_c('p',{staticStyle:{"margin":"50px","text-align":"justify"}},[_vm._v(" Nos comprometemos a ofrecerte opciones de privacidad útiles. Tienes varias opciones para controlar o limitar la manera en que nosotros, nuestros socios y otras empresas usamos las cookies: Para controlar si Aparcar almacena información sobre otros sitios web en los que has visto contenido de Aparcar, ajusta la opción de configuración Registrar dónde ves contenido de Aparcar en la web en la configuración de Personalización y datos. Si tienes esta configuración desactivada, o si te encuentras en la Unión Europea o en alguno de los estados miembro de la Asociación Europea de Libre Comercio, Aparcar no almacenará ni usará las visitas a dichas páginas web para mejorar tu experiencia en el futuro. Si almacenamos anteriormente tu historial de navegación web, tu experiencia puede seguir siendo personalizada sobre la base de la información que ya se infirió de dicho historial. Si no quieres que Aparcar te muestre anuncios basados en intereses dentro ni fuera de Aparcar, existen varias maneras de desactivar esta función: ")]),_c('p',{staticStyle:{"margin":"50px","text-align":"justify"}},[_vm._v("En tu configuración de Aparcar, ve a los ajustes de Personalización y datos y modifica la opción Personalizar anuncios. Desde la web, puedes visitar la herramienta de preferencias de los consumidores de Digital Advertising Alliance en optout.aboutads.info para desactivar los anuncios de Aparcar basados en los intereses en tu navegador actual. Solo en la versión 13 de iOS y versiones anteriores, si no deseas que Aparcar te muestre anuncios basados en intereses en Aparcar para iOS en el dispositivo móvil actual, habilita la opción de configuración Limitar seguimiento de anuncios en la configuración del teléfono iOS (las instrucciones exactas pueden ser diferentes en distintas versiones de iOS). En Android, si no deseas que Aparcar te muestre anuncios basados en intereses en Aparcar para Android en el dispositivo móvil actual, habilita Inhabilitar personalización de anuncios en la configuración del teléfono Android (las instrucciones exactas y el nombre de la configuración pueden ser diferentes en distintas versiones de Android y variantes de OEM de Android). Solo en la versión 14 de iOS y versiones posteriores, si no deseas que Aparcar acceda a tu identificador de iOS para fines de publicidad, desactiva la opción de configuración Permitir que las aplicaciones soliciten seguimiento en la configuración de iOS (las instrucciones exactas pueden ser diferentes en distintas versiones de iOS). Para controlar la personalización en todos los dispositivos en Aparcar, ve a la configuración de Personalización y datos y modifica la configuración Personaliza en todos tus dispositivos. Esta configuración determina si podemos vincular tu cuenta con otros navegadores o dispositivos diferentes de los que usas para iniciar sesión en Aparcar (o bien, si no iniciaste sesión, si podemos vincular el navegador o el dispositivo que estás usando con otros navegadores o dispositivos). Si deseas controlar los anuncios segmentados en función de los intereses que recibes de determinados socios de publicidad externos, puedes obtener más información sobre cómo dejar de recibir este tipo de anuncios en optout.aboutads.info y en www.networkadvertising.org/choices. Si estás en la versión web, también puedes deshabilitar Google Analytics si instalas el complemento de inhabilitación para navegadores de Google, y puedes cancelar el servicio de anuncios de Google basados en intereses mediante la configuración de anuncios de Google. Para controlar las cookies, puedes modificar tu configuración en la mayoría de los navegadores web para aceptar o rechazar cookies, o para que se solicite tu autorización cada vez que un sitio web intente establecer una cookie. Aunque las cookies no son necesarias en algunas partes de nuestros servicios, es posible que Aparcar y Periscope no funcionen correctamente si las desactivas por completo. Por ejemplo, no podrás iniciar sesión en twitter.com o en pscp.tv si desactivaste el uso de cookies por completo. ")]),_c('br')])])],1)],1),_c('v-footer',{staticClass:"footer",attrs:{"dark":"","padless":""}},[_c('v-card',{staticClass:"blue darken-1 white--text text-center flex",attrs:{"flat":"","tile":""}},[_c('v-card-text',[_vm._v(" 2020 Aparcar Inc ")]),_c('v-card-text',[_vm._v(" Contacta con nosotros: "),_c('v-icon',{attrs:{"size":"24px"}},[_vm._v(" mdi-instagram")]),_vm._v(" ")