<template>
        <div>
            <div class="wrapper">
            <v-container>
                <v-row dense>
                    <v-col >
                        
                        <!--v-img
                            src="https://i.ibb.co/vjBW777/mapa.png"
                            height="350"
                            class="grey darken-4"
                        ></v-img-->
                        <h1 class="blue--text" align="center">PARKINGS DE LEÓN</h1>
                         <div id="map" class="basemap pb-1"></div>
                    </v-col>
                    <v-col cols="12">
                        <v-expansion-panels>
                            <v-expansion-panel>
                            <v-col cols="2">
                            <v-expansion-panel-header class="filtro pb-4">
                                FILTRAR <v-icon small >mdi-filter</v-icon>
                            </v-expansion-panel-header>
                            </v-col>
                            <v-expansion-panel-content>
                                <v-row>
                                    
                                    <v-checkbox
                                        v-model="selected"
                                        label="Plazas movilidad reducida"
                                        value="minusvalidos"
                                        class="ml-4"
                                    ></v-checkbox>
                                    <v-checkbox
                                        v-model="selected"
                                        label="Cuenta con seguridad"
                                        value="seguridad"
                                        class="ml-4"
                                    ></v-checkbox>
                                    <v-checkbox
                                        v-model="selected"
                                        label="Disponibilidad de ascensor"
                                        value="ascensor"
                                        class="ml-4"
                                    ></v-checkbox>
                                    <v-checkbox
                                        v-model="selected"
                                        label="Horario 24h"
                                        value="24h"
                                        class="ml-4"
                                    ></v-checkbox>
                                    <v-checkbox
                                        v-model="selected"
                                        label="Servicios públicos"
                                        value="servicios"
                                        class="ml-4"
                                    ></v-checkbox>
                                    
                                </v-row>
                            </v-expansion-panel-content>
                            </v-expansion-panel>
                        </v-expansion-panels>
                    </v-col>
                    <v-col 
                        v-for="(item, i) in filtro"
                        :key="i"
                        cols="12"
                        
                    >
                        <v-card @click="plazas(item.nombre)">
                            <div class="d-flex flex-no-wrap justify-space-between">
                                <div>
                                    <v-row>
                                    <div class="zoom">
                                    <v-avatar
                                        class="ml-7 mt-4"
                                        size="170"
                                        tile
                                    >
                                        <v-img :src="item.src"></v-img>
                                    </v-avatar>
                                    </div>
                                    <v-col>
                                    <v-card-title
                                        class="headline"
                                        v-text="item.nombre"
                                    ></v-card-title>
                                    <v-card-subtitle
                                        v-text="item.calle"
                                    >
                                    </v-card-subtitle>
                                    <v-card-text>Plazas libres: <font color="#1e88e5">{{item.plazas}}</font></v-card-text>
                                    <v-chip class="mt-3 ml-7 mb-6" color="blue darken-1" dark>{{item.precio}}</v-chip>
                                    <v-chip class="mt-3 ml-5 mb-6" color="blue darken-1" dark>{{item.horario}}</v-chip>
                                    <v-icon v-show="item.minusvalidos==true" title="minusválidos" class= "ml-5 mb-6" large right >mdi-wheelchair-accessibility</v-icon>
                                    <v-icon v-show="item.elevator==true" title="ascensor" class= "ml-5 mb-6" large right >mdi-elevator-passenger</v-icon>
                                    <v-icon v-show="item.seguridad==true" title="vigilancia" class= "ml-5 mb-6" large right >videocam</v-icon>
                                    <v-icon v-show="item.servicio==true" title="servicio" class= "ml-5 mb-6" large right >mdi-toilet</v-icon>
                                    </v-col>
                                    </v-row>
                                </div>
                            </div>
                        </v-card>
                        
                    </v-col>
                </v-row>
                <br>
                <br>
            </v-container>
            </div>
             
              <v-footer
                dark
                padless
                class="footer"
                >
                <v-card
                flat
                tile
                class="blue darken-1 white--text text-center flex">
                    <v-card-text>
                        2020 Aparcar Inc
                    </v-card-text>
                    <v-card-text>
                        Contacta con nosotros: <v-icon  size="24px">  mdi-instagram</v-icon> <v-icon  size="24px">mdi-facebook</v-icon><v-icon size="24px">mdi-twitter</v-icon>
                    </v-card-text>
                </v-card>
                </v-footer>  
        </div>
</template>

<script>
// @ is an alias to /src
//import GoogleMap from '@/components/GoogleMap';

//import LeafletMap from '@/components/Map';
import mapboxgl from "mapbox-gl";
export default {
  name: 'App',
  

  data: () => ({
        accessToken: 'pk.eyJ1IjoiaWdvbnpmMDYiLCJhIjoiY2tqYnU4eHp2MGFrNTJzbnpnN3RqdnNvOSJ9.2kKjbbdmtN2JIZpdm5BepQ',
       
      selected:[],
      items: [
          {
              nombre: 'Parking Eras de Renueva',
              calle:'Av. los Peregrinos, 2, 24008, León, España',
              plazas:'20',
              horario:'24h',
              precio:'1,20€/h',
              seguridad:true,
              minusvalidos: true,
              elevator:true,
              servicio: false,
              src:'https://estaticos2.ileon.com/resources/files/2019/12/27/1577437877216SanMarcosParkingdn.jpg',
          },
            {
              nombre: 'Hotel Silken León',
              calle:'Calle Octavio Alvarez Carballo, 10, 24005, León, España',
              plazas:'5',
              horario:'24h',
              precio:'1,26€/h',
              seguridad:true,
              minusvalidos: true,
              elevator:false,
              servicio: true,
              src:'https://s3-eu-west-1.amazonaws.com/elparking/7fee7b8e0cf6a1a71a03b8fc7a5e39f0/IMG64249095d54e1f9bf6789d9db5b6e6ca.jpg',
          },
            {
              nombre: 'Parking Ordoño II',
              calle:'Avda. Ordoño II, 39, 24001, León, España',
              plazas:'140',
              horario:'24h',
              precio:'1,81€/h',
              seguridad:true,
              minusvalidos: true,
              elevator: true,
              servicio: false,
              src:'https://estaticos2.ileon.com/resources/files/2019/2/1/1549016832291Ordono1dn.jpg',
          },
      ],
      
      
      
      
  }),
  mounted() {
    
    
	mapboxgl.accessToken = "pk.eyJ1Ijoic29sdmVyZSIsImEiOiJjazBtOWpscHkwN2U4M2R0N28wYzIxMG03In0.EyWjgHJmdrVlkhM6wyE5Uw";
	/* Map: This represents the map on the page. */
	var map = new mapboxgl.Map({
		container: "map",
		style: "mapbox://styles/mapbox/streets-v11",
        zoom: 17,
        center: [-5.57032, 42.60003],
	});
	map.addControl(new mapboxgl.NavigationControl());
	map.addControl(new mapboxgl.FullscreenControl());
	map.on("load", function () {
		map.loadImage("https://i.ibb.co/K24FfQm/png-transparent-black-p-gps-illustration-computer-icons-car-park-iconfinder-symbols-parking-miscella.png", function(error, image) {
		if (error) throw error;
		map.addImage("custom-marker", image);
		map.addLayer({
                id: "markers1",
                type: "symbol",
                source: {
                    type: "geojson",
                    data: {
                        type: 'FeatureCollection',
                        features: [
                        {
                            type: 'Feature',
                            properties: {},
                            geometry: {
                                type: "Point",
                                coordinates: [-5.583867284688188, 42.60697022668996]
                            }
                        }
                        ]
                    }
                },
                layout: {
                "icon-image": "custom-marker",
                }
            });
        });
        map.addLayer({
                id: "markers2",
                type: "symbol",
                source: {
                    type: "geojson",
                    data: {
                        type: 'FeatureCollection',
                        features: [
                        {
                            type: 'Feature',
                            properties: {},
                            geometry: {
                                type: "Point",
                                coordinates: [-5.568357095101666, 42.59102046675677]
                            }
                        }
                        ]
                    }
                },
                layout: {
                "icon-image": "custom-marker",
                }
            });
            map.addLayer({
                id: "markers3",
                type: "symbol",
                source: {
                    type: "geojson",
                    data: {
                        type: 'FeatureCollection',
                        features: [
                        {
                            type: 'Feature',
                            properties: {},
                            geometry: {
                                type: "Point",
                                coordinates: [-5.577019974704085, 42.59667458280222]
                            }
                        }
                        ]
                    }
                },
                layout: {
                "icon-image": "custom-marker",
                }
            });
        });	   
  },
  methods:{
      plazas(name){
          this.$router.push({
              name: "plazas",
              params:{
                  id: name,
              }
            
          });
      },
      
  },
  computed:{
      filtro(){
          var f=[];
          if(this.selected.length==0){
              f=this.items;
          }else{
              for(var i = 0; i<this.items.length; i++){
                  var tiene = true;
                  for(var j = 0; j<this.selected.length; j++){
                      if(this.selected[j]=='minusvalidos' && !this.items[i].minusvalidos){
                          tiene=false;
                      }else if(this.selected[j]=='ascensor' && !this.items[i].elevator){
                          tiene=false;
                      }else if(this.selected[j]=='seguridad' && !this.items[i].seguridad){
                          tiene=false;
                      }else if(this.selected[j]=='servicios' && !this.items[i].servicio){
                          tiene=false;
                      }else if(this.selected[j]=='24h' && !this.items[i].horario=='24h'){
                        tiene=false;

                      }
                  }
                  if(tiene){
                      f.push(this.items[i]);
                  }
              }
              
          }
          return f;
      },
  },
}
</script>
<style scoped lang="scss">
.zoom{
   transition: transform 0.5; 
}
.zoom:hover {
transform: scale(1.2);
}
  .wrapper {
      min-height: calc(100vh - 120px);
    }
    .footer {
      position: relative;
      width: 100%;
      bottom: 0;    
    }
.filtro{font-family:'calibri';font-weight: bold;font-size:18px}
.router{color: #FFF;}
.router:hover{ color:none}
.basemap {
  width: 100%;
  height: 100%;
}
</style>