<template>
        <div class="prueba">
            <div class="wrapper">
            <v-container >
                <h1 align="center" class="blue--text">{{$route.params.id}}</h1><br>
                
                 <v-alert
                      dense
                      outlined
                      type="error"
                      v-model="dialogOcupada"
                    >
                      <v-row
                        align="center"
                        no-gutters
                      >
                        <v-col class="grow">
                          {{ocupada}}
                        </v-col>
                        <v-spacer></v-spacer>
                        <v-col class="shrink">
                          <v-btn
                            color="error"
                            outlined
                            @click="dialogOcupada=false"
                          >
                            CERRAR
                          </v-btn>
                        </v-col>
                      </v-row>
                      
                    </v-alert>
                
                        
                        <v-row dense>
                    <v-col cols="12">
                        <div>
                        <v-card >
                        <v-card-text>
                        <v-simple-table fixed-header height="300" width="10">
                            <thead >
                            <tr>
                              <th>
                                Número de plaza
                              </th>
                              <th>
                                Seleccionar
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr
                              v-for="item in plazas"
                              :key="item.numero"
                              v-show="show(item.title)"
                            >
                              <td>
                              
                                <v-chip
                                    :color="item.color"
                                    :title="item.title"
                                    dark
                                     v-if="color"
                                >
                                    {{ item.numero }} {{item.title}}
                                </v-chip>
                                <v-chip
                                    class="black--text"
                                    color="#FFFFFF"
                                    :title="item.title"
                                    dark
                                     v-else
                                >
                                    {{ item.numero }} {{item.title}}
                                </v-chip>
                                
                                </td>
                              <td><v-checkbox  :value="item.numero" v-model="plaza"></v-checkbox>  </td>
                            </tr>
                          </tbody>
                      </v-simple-table>
                      </v-card-text>
                      <v-card-actions>
                        <v-flex class="text-right">
                          <br>
                        <v-btn color="blue" class="white--text" @click="reservar()">Reservar</v-btn>
                        </v-flex>
                      </v-card-actions>
                      
                      </v-card>
                      <h4 v-if="!color">Si quiere activar la asignación de colores de las plazas pulse <a @click="cambiarColores()">aquí</a>.</h4> 
                      <h4 v-else>Si quiere desactivar la asignación de colores de las plazas pulse <a @click="cambiarColores()">aquí</a>.</h4> 
                      <br>
                    </div>
                    </v-col>
                      
                     
                      
                      
                      <v-col class="d-flex justify-center">
                        <br><br>
                    <!--  <v-card>
                        <v-card-text align="center">
                          <h3>Disposición de las plazas del aparcamiento {{$route.params.id}}</h3> <br>
                          <v-img src="https://i.ibb.co/m6FKbZZ/parking.png" class="imagen"/>
                        </v-card-text>
                      </v-card>-->

                        <v-carousel align="center" class="carousel-inner">
                        <h3 align="center">Disposición de las plazas del aparcamiento {{$route.params.id}}</h3> <br>

                        <v-carousel-item class="carousel-item-img"
                          v-for="(item,i) in items"
                          :key="i"
                          :src="item.src"
                      
                        ></v-carousel-item>
                      </v-carousel>

                     </v-col>
                   </v-row>

                 
                
              </v-container>
            </div>
               <v-footer
                dark
                padless
                class="footer"
                >
                <v-card
                flat
                tile
                class="blue darken-1 white--text text-center flex">
                    <v-card-text>
                        2020 Aparcar Inc
                    </v-card-text>
                    <v-card-text>
                        Contacta con nosotros: <v-icon  size="24px">  mdi-instagram</v-icon> <v-icon  size="24px">mdi-facebook</v-icon><v-icon size="24px">mdi-twitter</v-icon>
                    </v-card-text>
                </v-card>
                </v-footer>  
                
        </div>
</template>

<script>

  

export default {
  name: 'plazas',
    
  

  data: () => ({
     items: [
          {
            src: 'https://i.ibb.co/m6FKbZZ/parking.png',
          },
          {
            src: 'https://www.diariodeleon.es/media/diariodeleon/images/2016/11/22/1116678_1.jpg',
          },
          {
            src: 'https://digitaldeleon.com/wp-content/uploads/2020/02/parking-de-la-junta-de-castilla-y-le%C3%B3n.jpg',
          },
          {
            src: 'https://i.ibb.co/QF70p8J/logo1.png',
          },
        ],
  
    plazas: [
        {numero:'1',color:"green", title:"Libre"},
        {numero:'2',color:"red", title:"Ocupado"},
        {numero:'3',color:"red", title:"Ocupado"},
        {numero:'4',color:"red", title:"Ocupado"},
        {numero:'5',color:"orange", title:"Libre proximamente"},
        {numero:'6',color:"orange", title:"Libre proximamente"},
        {numero:'7',color:"green", title:"Libre"},
        {numero:'8',color:"green", title:"Libre"},
        {numero:'9',color:"green", title:"Libre"},
        {numero:'10',color:"green", title:"Libre"},
        {numero:'11',color:"green", title:"Libre"},
        {numero:'12',color:"green", title:"Libre"},
        {numero:'13',color:"green", title:"Libre"},
        {numero:'14',color:"red", title:"Ocupado"},
        {numero:'15',color:"orange", title:"Libre proximamente"},
        {numero:'16',color:"red", title:"Ocupado"},
        {numero:'17',color:"blue", title:"Libre Minusválidos"},
        {numero:'18',color:"blue", title:"Ocupada Minusválidos"},
    ],
    plaza:'',

    dialogOcupada:false,
    ocupada:'',
    color: false,
    
  }),
  methods: {
      reservar(){
        var parking=this.$route.params.id;
        this.plaza = this.plazas.find(p => p.numero==this.plaza)
        
        if(this.plaza.title=='Ocupado'){
          this.dialogOcupada=true;
          this.ocupada='Esta plaza esta ocupada seleccione otra.'
        }else if(this.plaza.title=='Libre proximamente'){
          this.dialogOcupada=true;
          this.ocupada='Esta plaza esta ocupada y estará libre proximamente. Seleccione otra o espere a que quede libre.'
        }else if(this.plaza.title=='Minusválidos'){
          var t = this.plaza.numero.split(" ");
          if(t[1]=='Ocupada'){
            this.dialogOcupada=true;
            this.ocupada='Esta plaza de minusválidos esta ocupada selecciones otra.'
          }else{
            this.$router.push({
              name: "reservar",
              params:{
                  numero: this.plaza,
                  parking: parking,
              }
            
          });
          }
        }else{
          this.$router.push({
              name: "reservar",
              params:{
                  numero: this.plaza,
                  parking: parking,
              }
            
          });
        }
          
      },
      show(item){
        if(item=='Libre' || item=='Libre proximamente' || item=='Libre Minusválidos'){
          return true;
        }else{
          return false;
        }
      },
      cambiarColores(){
        this.color=!this.color;
        
      },
  },
};
</script>
<style scoped lang="scss">
 .wrapper {
      min-height: calc(100vh - 120px);
    }
    .footer {
      position: relative;
      width: 100%;
      bottom: 0;    
    }
  .tabla {
    height: 100%;
    width: 80%;
  } 
 .carousel-item-img {
    //width: 80%;
    max-height: 460px;
}

.carousel-inner{
  width: 75%;
 height: 200px;
}
</style>