import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    session: false,
  },
  mutations: {
    updateSession(state, newSession) {
      state.session = newSession;
    },

  },
  getters:{
    session: state => state.session,
  },
  actions: {
  },
  modules: {
  }
})
