import Vue from 'vue'
import Router from 'vue-router'
import Inicio from '@/components/Inicio.vue'
import Registro from '@/components/Registro.vue'
import Contraseña from '@/components/Contraseña.vue'
import Plazas from '@/components/Plazas.vue'
import Reservar from '@/components/Reservar.vue'
import Principal from '@/components/Principal.vue'
import ConCuenta from '@/components/ConCuenta.vue'
import SinCuenta from '@/components/SinCuenta.vue'
import Condiciones from '@/components/Condiciones.vue'
import PoliticaDeDatos from '@/components/PoliticaDeDatos.vue'
import PoliticaDeCookies from '@/components/PoliticaDeCookies.vue'
import AppBar from '@/components/AppBar.vue';
Vue.use(Router)
export default new Router({
  
  routes: [
    {
      path: '/',
      name: 'appBar',
      component: AppBar,
      children: [
        {
          path: '/',
          name: 'principal',
          component: Principal,
        },
        {
          path: '/inicio',
          name: 'inicio',
          component: Inicio,
        },
        {
          path: '/registro',
          name: 'registro',
          component: Registro,
        },
        {
        path: '/contrasenia',
        name: 'contraseña',
        component: Contraseña,
        },
        
        {
          path: '/plazas/:id',
          name:'plazas',
          component: Plazas,
        },
        {
          path: '/reservar/:numero/:parking',
          name: 'reservar',
          component: Reservar,
        },
        {
          path: '/cuenta',
          name: 'conCuenta',
          component: ConCuenta,
        },
        {
          path: '/nocuenta',
          name: 'sinCuenta',
          component: SinCuenta,
        },
        {
          path: '/condiciones',
          name: 'condiciones',
          component: Condiciones,
        },
        {
          path: '/politicaDeDatos',
          name: 'politicaDeDatos',
          component: PoliticaDeDatos,
        },
        {
          path: '/politicaDeCookies',
          name: 'politicaDeCookies',
          component: PoliticaDeCookies,
        }
      ]
    },
    
    {
      path: '*',
      redirect: {
        name: 'appBar',
      },
    },
    
    
  ]
});

